.eboard_header{
    height: 500px;
    background-image: url(../eboard.jpg);
    background-size: cover;
    background-attachment: fixed;
    background-position: top center;
    position: relative;
}

.eboard_header h1{
    font-size: 50px;
    color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.eboard_members{
    background-color: #191a52;
    padding-top: 150px;
}

.eboard_members h1{
    color: white;
    text-align: center;
    font-size: 35px;
    margin-bottom: 50px;
}

.eboard_members .eboard-years{
    color: white;
    text-align: center;
    font-size: 25px;
    margin-bottom: 200px;
}

@media (max-width: 570px){
    .eboard_header h1{
        font-size: 40px;
        text-align: center;
    }
    .eboard_members h1{
        font-size: 30px;
    }

    .eboard_members .eboard-years{
        font-size: 22px;
    }
}